<template>
  <div>
    <md-whiteframe class="header-custom" md-tag="md-toolbar" md-elevation="3">
      <div class="md-toolbar-container">
        <md-button @click.native="toggleLeftSidenav" class="btn-sidebar">
          <md-tooltip md-direction="top">Menu</md-tooltip>
          <md-icon>menu</md-icon>
        </md-button>
        <h1 class="md-title">
          <!--{{myInfo.name}}-->
          <!--<vue-typer :text='[myInfo.name, myInfo.title, myInfo.subTitle]' initial-action='typing' :pre-type-delay='70' :type-delay='70' :pre-erase-delay='2000' :erase-delay='100' erase-style='backspace'></vue-typer>-->
          <!--<br/><span class="md-subheading">{{myInfo.title}}</span>-->
        </h1>
        <span style="flex: 1;">
        </span>
        <md-speed-dial class="md-fab-bottom-right fab-custom" md-direction="left" :class="{'static-speed-dial': scrollButtonVisible}">
          <md-button class="md-fab animate-attention" md-fab-trigger>
            <md-icon md-icon-morph>close</md-icon>
            <md-icon>mail</md-icon>
            <md-tooltip md-direction="top">Contact Now</md-tooltip>
          </md-button>
          <md-button class="md-fab md-mini" @click.native="sendEmail">
            <md-icon>mail_outline</md-icon>
            <md-tooltip md-direction="top">by Email</md-tooltip>
          </md-button>
          <md-button class="md-fab md-mini" @click.native="goto ('Contact')">
            <md-icon>chat_bubble_outline</md-icon>
            <md-tooltip md-direction="top">by Contact Form</md-tooltip>
          </md-button>
        </md-speed-dial>
      </div>
    </md-whiteframe>
    <md-sidenav class="main-sidebar md-left md-fixed" md-swipeable ref="leftSidenav">
      <md-whiteframe class="header-custom" md-tag="md-toolbar" md-elevation="0">
        <div class="md-toolbar-container"></div>
        <md-list class="sidebar-menu">
          <md-list-item @click.native="goto('About')">
            <md-icon class="md-accent">info_outline</md-icon>
            <span>About</span>
            <md-ink-ripple />
          </md-list-item>
          <md-list-item @click.native="goto('Skills')">
            <md-icon class="md-accent">view_list</md-icon>
            <span>Skills</span>
            <md-ink-ripple />
          </md-list-item>
          <md-list-item @click.native="goto('Services')">
            <md-icon class="md-accent">assignment</md-icon>
            <span>Services</span>
            <md-ink-ripple />
          </md-list-item>
          <md-list-item @click.native="goto('Portfolio')">
            <md-icon class="md-accent">view_carousel</md-icon>
            <span>Portfolio</span>
            <md-ink-ripple />
          </md-list-item>
          <md-list-item @click.native="goto('Contact')">
            <md-icon class="md-accent">mail_outline</md-icon>
            <span>Contact</span>
            <md-ink-ripple />
          </md-list-item>
        </md-list>
      </md-whiteframe>
    </md-sidenav>
    <a ref="link_email" style="display: none; visibility: hidden;" href="mailto:${myInfo.email}?Subject=HireMe">Email Me</a>
    <!--<transition name="slide-right" appear mode="out-in">
      <div class="btn-menu-custom" v-if="scrollButtonVisible">
        <md-button class="md-fab md-clean" @click.native="toggleLeftSidenav">
          <md-icon>menu</md-icon>
        </md-button>
      </div>
    </transition>-->
    <transition name="slide-left" appear mode="out-in">
      <div class="btn-scroll-to-top" v-if="scrollButtonVisible">
        <md-button class="md-fab md-clean" @click.native="gotoTop">
          <md-icon class="md-accent">arrow_upward</md-icon>
        </md-button>
      </div>
    </transition>
  </div>
</template>
<script>
  import debounce from 'lodash.debounce'
  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        scrollButtonVisible: false
      }
    },
    components: {},
    watch: {},
    computed: {
      ...mapGetters(['myInfo', 'sections'])
    },
    methods: {
      sendEmail () {
        this.$refs['link_email'].click()
      },
      goto (section) {
        this.$refs.leftSidenav.close()
        window.setTimeout(() => {
          let sectionId = 'section-' + section.toLowerCase()
          document.getElementById(sectionId).scrollIntoView({ behavior: 'smooth' })
          window.setTimeout(() => {
            if (section.toLowerCase() === 'contact') {
              document.getElementById('txt_email').focus()
            }
          }, 250)
        }, 250)
      },
      gotoTop () {
        window.scroll({ top: 0, left: 0, behavior: 'smooth' })
      },
      toggleLeftSidenav () {
        this.$refs.leftSidenav.toggle()
      }
    },
    created () {
      var _self = this
      document.body.onscroll = debounce(function () {
        _self.scrollButtonVisible = document.body.scrollTop > 180
      }, 200, {
        leading: false,
        trailing: true
      })
    },
    mounted () {

    }
  }
</script>
<style scoped>
  .md-list-item {
    cursor: pointer;
  }
  
  .btn-scroll-to-top {
    position: fixed;
    right: 16px;
    bottom: 76px;
    z-index: 5;
  }
  
  .btn-menu-custom {
    position: fixed;
    left: 16px;
    top: 16px;
    z-index: 5;
  }
  
  /*.static-speed-dial {}*/
</style>



