<template>
  <md-layout>
    <h1 class="md-headline">
      {{sections.skills.heading}}
      <br/>
      <span class="md-caption" v-html="sections.skills.subheading"></span>
    </h1>
    <div>
      <md-chip class="custom-chip" v-for="skill in skills" :key="skill.title">{{ skill.title }}</md-chip>
    </div>
    <br/>
  </md-layout>
</template>
<script>
  import { mapGetters } from 'vuex'

  export default {
    computed: {
      ...mapGetters(['skills', 'sections'])
    }
  }
</script>
<style>
  .custom-chip {
    margin: 0 3px 3px 0;
    font-weight: lighter;
    background-color: #3f51b5!important;
    color: #fff;
  }
</style>



