<template>
  <md-layout>
    <div class="full-width">
      <h1 class="md-headline">
        {{sections.contact.heading}}
        <br/>
        <span class="md-caption" v-html="sections.contact.subheading"></span>
      </h1>
    </div>
    <md-whiteframe md-elevation="1" class="full-width contact-form">
      <form novalidate @submit.stop.prevent="submit">
        <md-spinner :md-size="40" md-indeterminate class="md-accent progress-custom" :class="{ 'show': loading}"></md-spinner>
        <md-input-container :class="{'md-input-invalid': !isEmailValid}">
          <label>Your Email Address</label>
          <md-input id="txt_email" v-model="email" :readonly="loading"></md-input>
          <span class="md-error">Please enter a valid Email address</span>
        </md-input-container>
        <md-input-container :class="{'md-input-invalid': !isMessageValid}">
          <label>Message</label>
          <md-textarea v-model="message" minlength="500" maxlength="500" :readonly="loading"></md-textarea>
          <span class="md-error">Please enter some message</span>
        </md-input-container>
        <md-button type="submit" class="md-raised md-accent btn-custom" :disabled="loading">
          <span v-if="!loading">Send</span>
          <span v-else>Sending</span>
        </md-button>
      </form>
    </md-whiteframe>
    <md-snackbar ref="validation_snackbar" md-duration="5000">
      <md-icon>report_problem</md-icon> &nbsp;
      <span>Please provide required fields.</span>
      <md-button class="md-accent" @click.native="$refs.validation_snackbar.close()">Retry!</md-button>
    </md-snackbar>
    <md-snackbar ref="error_snackbar" md-duration="5000">
      <md-icon>report_problem</md-icon> &nbsp;
      <span>Error!</span>
      <md-button class="md-accent" @click.native="submit">Retry!</md-button>
    </md-snackbar>
    <md-snackbar ref="success_snackbar" md-duration="10000">
      <md-icon>done</md-icon> &nbsp;
      <span>Message sent successfully!</span>
      <md-button class="md-accent" @click.native="$refs.success_snackbar.close()">Great!</md-button>
    </md-snackbar>
  </md-layout>
</template>
<script>
  import { mapGetters } from 'vuex'
  import reqwest from 'reqwest'

  export default {
    data () {
      return {
        email: '',
        message: '',
        loading: false,
        validation: {
          email: {
            dirty: false
          },
          message: {
            dirty: false
          }
        }
      }
    },
    computed: {
      ...mapGetters(['myInfo', 'sections']),
      isEmailValid () {
        return this.validation.email.dirty ? /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(this.email) : true
      },
      isMessageValid () {
        return this.validation.message.dirty ? this.message.length > 10 : true
      }
    },
    methods: {
      submit () {
        var _self = this

        _self.dirtyFields()

        if (!_self.isEmailValid || !_self.isMessageValid) return
        _self.loading = true

        reqwest({
          url: _self.myInfo.formspreeURL,
          method: 'post',
          data: {
            email: _self.email,
            message: _self.message
          },
          success: function () {
            _self.$refs['success_snackbar'].open()
          },
          error: function () {
            _self.$refs['error_snackbar'].open()
          },
          complete: function () {
            _self.loading = false
          }
        })
      },
      dirtyFields () {
        this.validation.email.dirty = true
        this.validation.message.dirty = true
      }
    },
    mounted () {
    }
  }
</script>
<style>
  .btn-custom {
    margin-left: 0!important;
    margin-bottom: 0!important;
  }
  
  .progress-custom {
    float: right;
    top: 20px;
    visibility: hidden;
  }
  
  .progress-custom.show {
    visibility: visible;
  }
  
  .contact-form {
    background-color: #3f51b5;
  }
  
  .contact-form form {
    margin-top: 4px;
    padding: 25px;
    padding-top: 0;
    background-color: #fff;
    /*background-color: rgba(255, 255, 255, 0.8);*/
  }
</style>



