<template>
  <md-layout>
    <h1 class="md-headline">
      {{sections.portfolio.heading}}
      <br/>
      <span class="md-caption" v-html="sections.portfolio.subheading"></span>
    </h1>
    <md-whiteframe md-elevation="0" class="full-width">
    </md-whiteframe>
    <br/>
  </md-layout>
</template>

<script>
  import { mapGetters } from 'vuex'

  const pathToImgs = require.context('assets/img', true)

  export default {
    computed: {
      ...mapGetters(['myInfo', 'sections', 'portfolio'])
    },
    methods: {
      imageSource (src) {
        console.log(src)
        return pathToImgs(src, true)
      }
    },
    mounted () { }
  }
</script>

<style>
  
</style>



