<template>
  <md-layout>
    <h1 class="md-headline">
      {{sections.about.heading}}
      <br/>
      <span class="md-caption" v-html="sections.about.subheading"></span>
    </h1>
    <md-whiteframe md-elevation="1" class="full-width">
      <div class="padded-container">
        <md-layout>
          <md-layout>
            <md-list class="md-double-line full-width">
              <md-list-item>
                <md-icon class="md-primary">person</md-icon>
                <div class="md-list-text-container">
                  <span v-html="myInfo.name"></span>
                  <span>Name</span>
                </div>
              </md-list-item>
              <md-divider></md-divider>
            </md-list>
          </md-layout>
          <md-layout>
            <md-list class="md-double-line full-width">
              <md-list-item>
                <md-icon class="md-primary">mail</md-icon>
                <div class="md-list-text-container">
                  <span v-html="myInfo.email"></span>
                  <span>Email</span>
                </div>
              </md-list-item>
              <md-divider></md-divider>
            </md-list>
          </md-layout>
        </md-layout>
        <p v-html="sections.about.summary"></p>
        <h2 class="md-title">Objective</h2>
        <p v-html="sections.about.objective"></p>
        <br/>
        <div class="profile-icons full-width">
          <a v-for="profile in myInfo.profiles" :href="profile.url">
            <md-icon>{{profile.icon}}</md-icon>&nbsp; {{profile.title}}
          </a>
        </div>
      </div>
    </md-whiteframe>
  </md-layout>
</template>
<script>
  import { mapGetters } from 'vuex'

  export default {
    computed: {
      ...mapGetters(['myInfo', 'sections'])
    }
  }
</script>
<style>
  .profile-icons a {
    display: block;
    width: 100%;
    padding: 10px;
    padding-left: 0;
  }
  
  @media (min-width: 760px) {
    .profile-icons a {
      display: inline-block;
      width: 20%;
      padding: 10px;
    }
  }
</style>



